import { z } from 'zod'
import { BookStyle, BOOK_CONSTRAINTS } from '../types/book'

// User Validation Schemas
export const loginSchema = z.object({
  username: z.string().email('Email inválido').min(1, 'Email é obrigatório'),
  password: z.string().min(1, 'Senha é obrigatória'),
})

export const registerSchema = z.object({
  email: z.string().email('Email inválido').min(1, 'Email é obrigatório'),
  password: z
    .string()
    .min(8, 'Senha deve ter pelo menos 8 caracteres')
    .regex(/[A-Z]/, 'Senha deve conter pelo menos uma letra maiúscula')
    .regex(/[a-z]/, 'Senha deve conter pelo menos uma letra minúscula')
    .regex(/[0-9]/, 'Senha deve conter pelo menos um número')
    .regex(/[^A-Za-z0-9]/, 'Senha deve conter pelo menos um caractere especial'),
  full_name: z
    .string()
    .min(2, 'Nome deve ter pelo menos 2 caracteres')
    .max(100, 'Nome deve ter no máximo 100 caracteres')
    .regex(/^[a-zA-ZÀ-ÿ\s\-\.\']+$/, 'Nome contém caracteres inválidos'),
})

export const changePasswordSchema = z.object({
  current_password: z.string().min(1, 'Senha atual é obrigatória'),
  new_password: z
    .string()
    .min(8, 'Nova senha deve ter pelo menos 8 caracteres')
    .regex(/[A-Z]/, 'Nova senha deve conter pelo menos uma letra maiúscula')
    .regex(/[a-z]/, 'Nova senha deve conter pelo menos uma letra minúscula')
    .regex(/[0-9]/, 'Nova senha deve conter pelo menos um número')
    .regex(/[^A-Za-z0-9]/, 'Nova senha deve conter pelo menos um caractere especial'),
  confirm_password: z.string().min(1, 'Confirmação de senha é obrigatória'),
}).refine((data) => data.new_password === data.confirm_password, {
  message: 'Senhas não coincidem',
  path: ['confirm_password'],
})

export const updateProfileSchema = z.object({
  email: z.string().email('Email inválido').min(1, 'Email é obrigatório'),
  full_name: z
    .string()
    .min(2, 'Nome deve ter pelo menos 2 caracteres')
    .max(100, 'Nome deve ter no máximo 100 caracteres')
    .regex(/^[a-zA-ZÀ-ÿ\s\-\.\']+$/, 'Nome contém caracteres inválidos'),
})

// Book Validation Schemas
export const bookCreateSchema = z.object({
  title: z
    .string()
    .min(BOOK_CONSTRAINTS.MIN_TITLE_LENGTH, `Título deve ter pelo menos ${BOOK_CONSTRAINTS.MIN_TITLE_LENGTH} caracteres`)
    .max(BOOK_CONSTRAINTS.MAX_TITLE_LENGTH, `Título deve ter no máximo ${BOOK_CONSTRAINTS.MAX_TITLE_LENGTH} caracteres`)
    .trim(),
  description: z
    .string()
    .max(BOOK_CONSTRAINTS.MAX_DESCRIPTION_LENGTH, `Descrição deve ter no máximo ${BOOK_CONSTRAINTS.MAX_DESCRIPTION_LENGTH} caracteres`)
    .optional()
    .or(z.literal('')),
  pages_count: z
    .number()
    .int('Número de páginas deve ser um número inteiro')
    .min(BOOK_CONSTRAINTS.MIN_PAGES, `Livro deve ter pelo menos ${BOOK_CONSTRAINTS.MIN_PAGES} páginas`)
    .max(BOOK_CONSTRAINTS.MAX_PAGES, `Livro deve ter no máximo ${BOOK_CONSTRAINTS.MAX_PAGES} páginas`),
  style: z.nativeEnum(BookStyle, {
    errorMap: () => ({ message: 'Estilo de livro inválido' })
  }),
})

export const bookUpdateSchema = bookCreateSchema.partial()

// Search and Filter Schemas
export const bookSearchSchema = z.object({
  search: z.string().optional(),
  status_filter: z.string().optional(),
  style_filter: z.nativeEnum(BookStyle).optional(),
  sort_by: z.enum(['created_at', 'title', 'updated_at']).optional(),
  sort_order: z.enum(['asc', 'desc']).optional(),
  skip: z.number().int().min(0).optional(),
  limit: z.number().int().min(1).max(100).optional(),
})

// File Upload Schema
export const fileUploadSchema = z.object({
  file: z
    .any()
    .refine((file) => file instanceof File, 'Arquivo é obrigatório')
    .refine((file) => file.size <= 10 * 1024 * 1024, 'Arquivo deve ter no máximo 10MB')
    .refine(
      (file) => ['image/jpeg', 'image/jpg', 'image/png', 'application/pdf'].includes(file.type),
      'Formato de arquivo não suportado. Use JPG, PNG ou PDF'
    ),
})

// Pagination Schema
export const paginationSchema = z.object({
  skip: z.number().int().min(0).default(0),
  limit: z.number().int().min(1).max(100).default(20),
})

// Type exports for forms
export type LoginFormData = z.infer<typeof loginSchema>
export type RegisterFormData = z.infer<typeof registerSchema>
export type ChangePasswordFormData = z.infer<typeof changePasswordSchema>
export type UpdateProfileFormData = z.infer<typeof updateProfileSchema>
export type BookCreateFormData = z.infer<typeof bookCreateSchema>
export type BookUpdateFormData = z.infer<typeof bookUpdateSchema>
export type BookSearchFormData = z.infer<typeof bookSearchSchema>
export type FileUploadFormData = z.infer<typeof fileUploadSchema>
export type PaginationFormData = z.infer<typeof paginationSchema>

// Validation helpers
export const validateSchema = <T>(schema: z.ZodSchema<T>, data: unknown): { success: true; data: T } | { success: false; error: z.ZodError } => {
  const result = schema.safeParse(data)
  if (result.success) {
    return { success: true, data: result.data }
  }
  return { success: false, error: result.error }
}

export const getFieldErrors = (error: z.ZodError): Record<string, string> => {
  const fieldErrors: Record<string, string> = {}
  error.errors.forEach((err) => {
    const field = err.path.join('.')
    fieldErrors[field] = err.message
  })
  return fieldErrors
}